import { Component, Directive, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-status-image',
  templateUrl: './status-image.component.html',
  styleUrls: ['./status-image.component.scss'],
})
@Directive({
  selector: 'app-status-image',
})
export class StatusImageComponent implements OnInit {
  @Input() width: string = '100%';
  @Input() height: string = '100%';
  @Input() loadingImg: string = 'assets/images/loading_default.svg';
  @Input() errorImg: string = 'assets/images/status_404.svg';
  @Input() src: string = '';
  @Input() alt: string = '';

  @Output() loadFinish: EventEmitter<any> = new EventEmitter();
  @Output() loadError: EventEmitter<any> = new EventEmitter();

  status: number = 0;

  constructor() {}

  ngOnInit() {}

  onSetSource() {
    this.status = 0;
  }

  onLoadFinish() {
    this.status = 1;
    this.loadFinish.emit('load image: {' + this.src + '} success');
  }

  onLoadError() {
    this.status = 2;
    this.loadError.emit('load image: {' + this.src + '} failed');
  }
}
