export class StringHelper {
  /**
   * overflow ellipsis
   * @param value
   * @param length
   * @param direction
   * @returns
   */
  public static ellipsis(
    value: string,
    length: number = 8,
    direction?: 'start' | 'center' | 'end'
  ) {
    direction = direction || 'center';
    if (!value || value.length <= length) {
      return value;
    }

    if (direction === 'start') {
      return '...' + value.substr(value.length - length);
    } else if (direction === 'center') {
      const cutIndex = length % 2 === 0 ? length / 2 : (length + 1) / 2;
      const prefix = value.substr(0, cutIndex);
      const sufix = value.substr(value.length - (length - cutIndex));
      return prefix + '...' + sufix;
    } else {
      return value.substr(0, length) + '...';
    }
  }

  /**
   * replace emoji symbols
   * @param source
   * @param target
   * @returns
   */
  public static replaceEmojis(source: string, target: string = '') {
    const pattern = [
      '\ud83c[\udf00-\udfff]',
      '\ud83d[\udc00-\ude4f]',
      '\ud83d[\ude80-\udeff]',
    ];
    const reg = new RegExp(pattern.join('|'), 'g');
    return source.replace(reg, target);
  }

  /**
   * replace html tags
   * @param source
   * @param target
   * @returns
   */
  public static replaceHtmlTags(source: string, target: string = '') {
    const regex = /(<([^>]+)>)/gi;
    return source.replace(regex, target);
  }

  /**
   * test picture suffix
   * @param sourceURL
   * @param format
   * @returns
   */
  public static testPictureFormat(
    sourceURL: string,
    format: 'png' | 'jpg' | 'gif' = 'gif'
  ) {
    const regex =
      format === 'gif'
        ? /\.gif\b/i
        : format === 'png'
        ? /\.png\b/i
        : /\.jpg\b/i;
    return regex.test(sourceURL);
  }

  /**
   * format timestamp base on milli-second
   * @param timestamp
   * @param format
   * @returns
   */
  public static formatTimestamp(
    timestamp: number,
    format: string = 'yyyy-MM-dd hh:mm:ss'
  ) {
    const doFormat: Function = (time: Date, format: string) => {
      const object = {
        'M+': time.getMonth() + 1,
        'd+': time.getDate(),
        'h+': time.getHours(),
        'm+': time.getMinutes(),
        's+': time.getSeconds(),
        'q+': Math.floor((time.getMonth() + 3) / 3),
        S: time.getMilliseconds(),
      };
      if (/(y+)/.test(format)) {
        format = format.replace(
          RegExp.$1,
          (time.getFullYear() + '').substr(4 - RegExp.$1.length)
        );
      }
      for (const key in object) {
        if (new RegExp('(' + key + ')').test(format)) {
          format = format.replace(
            RegExp.$1,
            RegExp.$1.length === 1
              ? object[key]
              : ('00' + object[key]).substr(('' + object[key]).length)
          );
        }
      }
      return format;
    };
    return doFormat(new Date(timestamp), format);
  }

  /**
   * fixed number
   * @param source
   * @param fixed
   * @returns
   */
  public static toFixed(source: number, fixed: number = null) {
    const reg = new RegExp('^-?\\d+(?:.\\d{0,' + (fixed || -1) + '})?');
    return source.toString().match(reg)[0];
  }

  /**
   * format to history time, such as: 3 days ago
   * @param timeDiff
   * @param mode
   * @param format
   * @param level
   * @param overflow
   * @param unitConfig such as: { day: '{0} days ago', day_hour_minute: '{0} days {1} hours {2} minutes ago', hour: '{0} hours ago', hour_minute_second: '{0} hours {1} minites {2} seconds ago', minute: '{0} minutes ago', minute_second: '{0} minutes {1} seconds ago', second: '{0} seconds ago'}
   * @returns
   */
  public static formatHistoryTime(
    timeDiff: number,
    mode: 'simple' | 'normal' = 'simple',
    format: string = 'yyyy-MM-dd hh:mm:ss',
    level: 'day' | 'hour' | 'minute' | 'second' = null,
    overflow: number = 1000 * 60 * 60 * 24 * 30,
    unitConfig: {
      day: string;
      day_hour_minute: string;
      hour: string;
      hour_minute_second: string;
      minute: string;
      minute_second: string;
      second: string;
    } = {
      day: '{0}天前',
      day_hour_minute: '{0}天{1}小时{2}分钟前',
      hour: '{0}小时前',
      hour_minute_second: '{0}小时{1}分钟{2}秒前',
      minute: '{0}分钟前',
      minute_second: '{0}分钟 {1}秒前',
      second: '{0}秒前',
    }
  ) {
    timeDiff = Math.max(timeDiff, 0);

    if (timeDiff > overflow) {
      return StringHelper.formatTimestamp(
        new Date().getTime() - timeDiff,
        format
      );
    }

    const replacePlaceholder: Function = (source: string, params: string[]) => {
      if (source && source.length > 0) {
        const testRegExp = /(\{\d\})/;
        while (testRegExp.test(source)) {
          const index: number = parseInt(RegExp.$1.slice(1, -1));
          source = source.replace(
            new RegExp('\\{' + index + '\\}', 'g'),
            index >= params.length ? '' : arguments[index]
          );
        }
        return source;
      }
      return null;
    };

    if (level) {
      if (level === 'day') {
        const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
        const hours = Math.floor(
          (timeDiff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        const minutes = Math.floor((timeDiff % (1000 * 60 * 60)) / (1000 * 60));

        return mode === 'simple'
          ? replacePlaceholder(unitConfig.day, [days])
          : replacePlaceholder(unitConfig.day_hour_minute, [
              days,
              hours,
              minutes,
            ]);
      } else if (level === 'hour') {
        const hours = Math.floor(timeDiff / (1000 * 60 * 60));
        const minutes = Math.floor((timeDiff % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((timeDiff % (1000 * 60)) / 1000);

        return mode === 'simple'
          ? replacePlaceholder(unitConfig.hour, [hours])
          : replacePlaceholder(unitConfig.hour_minute_second, [
              hours,
              minutes,
              seconds,
            ]);
      } else if (level === 'minute') {
        const minutes = Math.floor(timeDiff / (1000 * 60));
        const seconds = Math.floor((timeDiff % (1000 * 60)) / 1000);

        return mode === 'simple'
          ? replacePlaceholder(unitConfig.minute, [minutes])
          : replacePlaceholder(unitConfig.minute_second, [minutes, seconds]);
      } else {
        const seconds = Math.floor(timeDiff / 1000);

        return replacePlaceholder(unitConfig.second, [seconds]);
      }
    } else {
      let withDay: boolean = false;
      let withHour: boolean = false;
      let withMinute: boolean = false;

      const days: number = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
      if (days > 0) {
        withDay = true;
      }

      const hours: number = Math.floor(
        (timeDiff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      if (hours > 0) {
        withHour = true;
      }

      const minutes: number = Math.floor(
        (timeDiff % (1000 * 60 * 60)) / (1000 * 60)
      );
      if (minutes > 0) {
        withMinute = true;
      }

      const seconds: number = Math.floor((timeDiff % (1000 * 60)) / 1000);

      if (withDay) {
        return mode === 'simple'
          ? replacePlaceholder(unitConfig.day, [days])
          : replacePlaceholder(unitConfig.day_hour_minute, [
              days,
              hours,
              minutes,
            ]);
      } else if (withHour) {
        return mode === 'simple'
          ? replacePlaceholder(unitConfig.hour, [hours])
          : replacePlaceholder(unitConfig.hour_minute_second, [
              hours,
              minutes,
              seconds,
            ]);
      } else if (withMinute) {
        return mode === 'simple'
          ? replacePlaceholder(unitConfig.minute, [minutes])
          : replacePlaceholder(unitConfig.minute_second, [minutes, seconds]);
      } else {
        return replacePlaceholder(unitConfig.second, [seconds]);
      }
    }
  }

  /**
   * format file size
   * @param value
   * @returns
   */
  public static formatFileSize(value: number): string {
    if (!value) {
      return '0 Bytes';
    }
    const units = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    let index = 0;
    index = Math.floor(Math.log(value) / Math.log(1024));
    let size: number = value / Math.pow(1024, index);
    size = parseInt(StringHelper.toFixed(size, 2));
    return size + ' ' + units[index];
  }

  /**
   * covert file to base64 string
   * @param file
   * @returns
   */
  public static toBase64(file: File): Promise<string | ArrayBuffer> {
    return new Promise((resolve, reject) => {
      try {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = (event) => {
          const dataURL: string | ArrayBuffer = event.target.result;
          resolve(dataURL);
        };
      } catch (error) {
        reject(error);
      }
    });
  }

  /**
   * remove rich-text inline style (currently just remove background and color)
   * @param styleAttritute
   * @returns
   */
  public static removeInlineStyle(styleAttritute: string): string {
    let result = styleAttritute || '';
    const removeBackgroundRegExp =
      /(style=")([a-zA-Z0-9:;\(\)\,\s:\-\/\"\#\.\?\%\!\&]*)(background[-]?[a-zA-Z]*:[z-zA-Z0-9\(\)\,\s:\-\/\"\#\.\?\%\!\&]*[;]?[\s]*)([a-zA-Z0-9:;\(\)\,\s:\-\/\"\#\.\?\%\!\&]*)(")/gi;
    const removeColorRegExp =
      /(style=")([a-zA-Z0-9:;\(\)\,\s:\-\/\"\#\.\?\%\!\&]*)(color:[z-zA-Z0-9\(\)\,\s:\-\/\"\#\.\?\%\!\&]*[;]?[\s]*)([a-zA-Z0-9:;\(\)\,\s:\-\/\"\#\.\?\%\!\&]*)(")/gi;
    result = result.replace(removeBackgroundRegExp, '$1$2$4$5');
    result = result.replace(removeColorRegExp, '$1$2$4$5');
    return result;
  }

  /**
   * read file to base64 string from url
   * @param url
   * @returns
   */
  public static transferUrl2Base64(url: string): Promise<string | ArrayBuffer> {
    return new Promise((resolve, reject) => {
      fetch(url)
        .then((response) => response.blob())
        .then((blob) => {
          const reader = new FileReader();
          reader.onloadend = () => {
            resolve(reader.result);
          };
          reader.readAsDataURL(blob);
        })
        .catch(console.error);
    });
  }

  /**
   * covert base64 string to file
   * @param base64Data
   * @param fileName
   * @returns
   */
  public static base642File(base64Data: string, fileName: string): File {
    const array: string[] = base64Data.split(',');
    const mimeType: string = array[0].match(/:(.*?);/)[1];
    const data: string = atob(array[1]);
    let length: number = data.length;
    const u8Array: Uint8Array = new Uint8Array(length);
    while (length--) {
      u8Array[length] = data.charCodeAt(length);
    }
    return new File([u8Array], fileName, { type: mimeType });
  }

  /**
   * covert image to file
   * @param url
   * @returns
   */
  public static transferImage2File(url: string): Promise<File> {
    return new Promise(async (resolve, reject) => {
      const fileName = url.substring(url.lastIndexOf('/') + 1);
      const dataUrl = await this.transferUrl2Base64(url);
      const file = this.base642File(dataUrl.toString(), fileName);
      resolve(file);
    });
  }

  /**
   * transfer hex color to rgba color
   * @param color
   * @param opacity
   * @returns
   */
  public static transferHexColor(color = '#333', opacity = 1.0) {
    color = color.toLowerCase();
    const pattern = /^#([0-9|a-f]{3}|[0-9|a-f]{6})$/;
    if (color && pattern.test(color)) {
      if (color.length === 4) {
        color =
          '#' + color[1] + color[1] + color[2] + color[2] + color[3] + color[3];
      }
      const temp = [];
      for (let i = 1; i < 7; i += 2) {
        temp.push(parseInt('0x' + color.slice(i, i + 2)));
      }
      return 'rgba(' + temp.join(',') + ',' + opacity + ')';
    }
    return color;
  }
}
