import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { ChooseMediaTypeComponent } from 'src/app/modals/choose-media-type/choose-media-type.component';
import { ScreenFitService } from 'src/app/providers/screen-fit.service';
import { ThemeService } from 'src/app/providers/theme.service';
import { RuntimeService } from 'src/app/services/runtime.service';
import { StringHelper } from 'src/app/utils/string-helper';

@Component({
  selector: 'app-common-header',
  templateUrl: './common-header.component.html',
  styleUrls: ['./common-header.component.scss'],
})
export class CommonHeaderComponent implements OnInit {
  navIndex: number = 0;
  haveUnreadMessage: boolean = false;

  constructor(
    private screenFitService: ScreenFitService,
    private runtime: RuntimeService,
    private themeService: ThemeService,
    private router: Router,
    private modalController: ModalController
  ) {}

  ngOnInit() {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        const url = event.url.split('?')[0];

        switch (url) {
          case '/about':
            break;
          default:
            this.navIndex = 0;
            break;
        }
      }
    });
  }

  onClickNavItem(index: number) {
    this.navIndex = index;
  }

  onClickLogo() {
    this.router.navigate(['/home'])
  }

  onClickSearch() {
    this.router.navigate(['/search']);
  }

  async onClickAddArticle() {
    const modal = await this.modalController.create({
      cssClass: 'choose-media-type-dialog',
      component: ChooseMediaTypeComponent
    });

    return await modal.present();
  }

  onClickAbout() {
    this.router.navigate(['/about']);
  }

  getHeaderStyle() {
    let result = {};
    if (this.themeService.immersive) {
      const background = 'linear-gradient(180deg, {0} 0%, {1} 100%)'
        .replace(
          '{0}',
          StringHelper.transferHexColor(
            this.themeService.backgroundColor,
            this.themeService.opacity
          )
        )
        .replace(
          '{1}',
          StringHelper.transferHexColor(this.themeService.backgroundColor, 0)
        );
      result = { background: background };
    }
    return result;
  }
}
