import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { EventBusService } from 'src/app/services/event-bus.service';

@Component({
  selector: 'app-status-content',
  templateUrl: './status-content.component.html',
  styleUrls: ['./status-content.component.scss'],
})
export class StatusContentComponent implements OnInit {
  public static STATUS_LOADING: number = 0;
  public static STATUS_SUCCESS: number = 1;
  public static STATUS_ERROR: number = 2;

  @Input() refresher: boolean = false;
  @Input() defaultStatus: number = StatusContentComponent.STATUS_LOADING;
  @Input() loadingText: string = '加载中...';
  @Input() errorIcon: string = 'assets/images/request_error.svg';
  @Input() errorTitle: string = '加载失败';
  @Input() errorText: string = '当前服务器繁忙，请稍后再试';
  @Input() retryText: string = '点击重试';

  @Input() loadingHtml: string = '';
  @Input() retryHtml: string = '';
  @Input() loadingClass: string = '';
  @Input() errorClass: string = '';

  @Output() refresh: EventEmitter<any> = new EventEmitter();
  @Output() retry: EventEmitter<any> = new EventEmitter();
  @Output() statusChange: EventEmitter<any> = new EventEmitter();

  pageStatus: number = StatusContentComponent.STATUS_LOADING;
  refreshToken: any = null;

  constructor(private eventBus: EventBusService) {}

  ngOnInit() {
    this.pageStatus = this.defaultStatus;
  }

  doRefresh(event: any) {
    this.refreshToken = event.target;
    this.refresh.emit(event);
  }

  onClickRetry() {
    this.retry.emit();
  }

  invokeRefreshComplete() {
    if (this.refreshToken) {
      this.refreshToken.complete();
    }
  }

  invokeLoading(loadingText: string = null) {
    if (loadingText) {
      this.loadingText = loadingText;
    }

    this.pageStatus = StatusContentComponent.STATUS_LOADING;
    this.statusChange.emit('loading');
    this.eventBus.emmitEvent('status-content:loading', null);
  }

  invokeSuccess() {
    this.pageStatus = StatusContentComponent.STATUS_SUCCESS;
    this.statusChange.emit('success');
    this.eventBus.emmitEvent('status-content:success', null);
    this.invokeRefreshComplete();
  }

  invokeError(
    errorIcon: string = null,
    errorTitle: string = null,
    errorText: string = null,
    retryText: string = null
  ) {
    if (errorIcon) {
      this.errorIcon = errorIcon;
    }
    if (errorTitle) {
      this.errorTitle = errorTitle;
    }
    if (errorText) {
      this.errorText = errorText;
    }
    if (retryText) {
      this.retryText = retryText;
    }

    this.pageStatus = StatusContentComponent.STATUS_ERROR;
    this.statusChange.emit('error');
    this.eventBus.emmitEvent('status-content:error', null);
    this.invokeRefreshComplete();
  }
}
