import { LoadingController, ToastController } from '@ionic/angular';
import { RuntimeService } from 'src/app/services/runtime.service';
import { ScreenFitService } from '../providers/screen-fit.service';
import { StringHelper } from '../utils/string-helper';

export abstract class BasePage {
  constructor(
    protected loadingCtrl: LoadingController,
    protected toastCtrl: ToastController,
    protected runtime: RuntimeService,
    protected screenFitService: ScreenFitService
  ) {}

  protected async showLoading(message: string) {
    const loader = await this.loadingCtrl.create({
      translucent: true,
      spinner: 'circles',
      message: message,
    });
    await loader.present();
    return loader;
  }

  protected async showToast(message: string) {
    const toast = await this.toastCtrl.create({
      message: message,
      duration: 2000,
      mode: 'md',
      position: 'bottom',
    });
    await toast.present();
    return toast;
  }

  protected setPageTitle(title: string) {
    document.title = title;
  }

  protected formatHistoryTime(timeDiff: number) {
    return StringHelper.formatHistoryTime(Math.abs(timeDiff));
  }
}
